*{
  margin:0;
  padding: 0;
  box-sizing: border-box;
}
.landingPageContainer {
  width: 100vw;
  height: 100vh;
  background: url("/public/background2.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  color: white;
}

.landingPageContainer nav {
  padding-inline: 1.2rem;
  padding-block: 1.6rem;
  display: flex;
  justify-content: space-between;
  align-items: center;

}

.landingPageContainer nav h2 {
  font-size: 1.8rem;
  font-weight: 500;
}

.landingPageContainer nav .navlist {
  display: flex;
  gap: 1.6rem;
  cursor: pointer;

}

.landingMainContainer {
  display: flex;
  justify-content: space-between;
  padding-inline: 3rem;
  height: 80vh;
  align-items: center;
}

.landingMainContainer>div:nth-child(1) {
  font-size: 2.2rem;
}

.landingMainContainer>div:nth-child(1) div[role='button'] {
  background: #08d5f0;
  width: fit-content;
  padding: 1rem;
  border-radius: 20px;
  margin-top: 1.9rem;
}

.landingMainContainer>div:nth-child(1) div[role='button'] a {
  text-decoration: none;
  color: white;
  font-size: 1.6rem;
}

.landingMainContainer>div:nth-child(2) img {
  height: 88vh;
  width: 75vh;
}

.navBar {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding-inline: 20px;
  text-align: center;

}
.meetContainer {
  display: flex;
  padding-inline: 20px;
  height: 80vh;
  gap: 10vw;
  justify-content: center;
}



.leftPanel {
  height: 100%;
  display: flex;
  align-items: center;

}

.rightPanel {
  display: flex;
  align-items: center;
}

.rightPanel > img {
  width: 30vw;
  height: auto;
  border-radius: 20px;
}


/* App.css */

/* Keyframes for animations */
@keyframes fadeIn {
  from {
      opacity: 0;
  }
  to {
      opacity: 1;
  }
}

@keyframes slideInLeft {
  from {
      transform: translateX(-100%);
  }
  to {
      transform: translateX(0);
  }
}

@keyframes slideInRight {
  from {
      transform: translateX(100%);
  }
  to {
      transform: translateX(0);
  }
}

/* Styles for the navBar */
.navBar {
  display: flex;
  justify-content: space-between;
  padding: 20px;
  background-color: #333;
  color: white;
  animation: fadeIn 1s ease-in-out;
}

.navBar h2 {
  margin: 0;
}

.navBar div {
  display: flex;
  align-items: center;
}

.navBar p {
  margin: 0 10px;
}

/* Styles for the meetContainer */
.meetContainer {
  display: flex;
  justify-content: space-between;
  padding: 40px;
  animation: fadeIn 1s ease-in-out;
}

.leftPanel, .rightPanel {
  flex: 1;
  padding: 20px;
}

/* Animation for leftPanel */
.leftPanel {
  animation: slideInLeft 1s ease-in-out;
}



/* Animation for rightPanel */
.rightPanel {
  animation: slideInRight 1s ease-in-out;
}





